	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Train In Vain","date":"13 Jul 2002","slug":"/2002-07-13-train-in-vain/","tags":"Y2002"},"html":"<p>To say the Christopher is obsessed by trains would be a gross understatement. So today while Mama was on the road working (see <a href=\"http://www.papascott.de/2002/07/13/1805.php#001805\">below</a>), he and I took the S-Bahn to Hamburg and had lunch at <a href=\"http://www.bahnhof-hamburg.de/wandelhalle.html\">Hauptbahnhof</a> (with a window seat to the platforms below). I was Christopher's hero of the day, or at least for the hour.</p>\n<p>Tomorrow if the weather is good we'll go to Hamburg's zoo <a href=\"http://www.hagenbeck.de/\"> Tierpark Hagenbeck</a>. Christopher can name most of the classic zoo animals, so he should have a good time.<br />\n <b>Update:</b> We decided to stay home, due to drizzle and lack of energy. We'll go some other time.</p>"},"next":{"fields":{"title":"German women keep glass ceiling intact","date":"13 Jul 2002","slug":"/2002-07-13-german-women-keep-glass-ceiling-intact/"}},"previous":{"fields":{"title":"Sunlog and uni.tb","date":"14 Jul 2002","slug":"/2002-07-14-sunlog-and-unitb/"}}},{"node":{"fields":{"title":"German women keep glass ceiling intact","date":"13 Jul 2002","slug":"/2002-07-13-german-women-keep-glass-ceiling-intact/","tags":"Y2002"},"html":"<p>A FAZ link for Mama: <a href=\"http://www.faz.com/IN/INtemplates/eFAZ/docmain.asp?rub={B1311FCE-FBFB-11D2-B228-00105A9CAF88}&amp;doc={080C60DD-C5AD-416C-99B0-DDA3F1720C45}\">German women keep glass ceiling intact - Business careers foresaken for motherhood</a> \"The share of women managers at the top echelons in Germany is 3.7 percent, according to the European Union's statistical office, Eurostat. Even in junior management, the share of women has stagnated at around 10 percent.... Well-educated women in their thirties recognize that, although a career sounds glamorous, it requires a tremendous amount of hard work. Fed up with the constant fights over positions and budgets, many women quit the field and become moms instead.\"</p>"},"next":{"fields":{"title":"Wal-Mart Whimpers in Germany","date":"12 Jul 2002","slug":"/2002-07-12-wal-mart-whimpers-in-germany/"}},"previous":{"fields":{"title":"Train In Vain","date":"13 Jul 2002","slug":"/2002-07-13-train-in-vain/"}}},{"node":{"fields":{"title":"Wal-Mart Whimpers in Germany","date":"12 Jul 2002","slug":"/2002-07-12-wal-mart-whimpers-in-germany/","tags":"Y2002"},"html":"<p>Expatica: <a href=\"http://www.expatica.com/germany.asp?pad=190,205,&amp;item_id=23903\">Wal-Mart to close two German stores</a>.  Not everything that Wal-Mart touches turns to gold. After entering the German market with a big bang a couple of years ago, their impact on German retailing has been, well, nil.</p>"},"next":{"fields":{"title":"faz in english now weekly","date":"11 Jul 2002","slug":"/2002-07-11-faz-in-english-now-weekly/"}},"previous":{"fields":{"title":"German women keep glass ceiling intact","date":"13 Jul 2002","slug":"/2002-07-13-german-women-keep-glass-ceiling-intact/"}}}],"pathPrefix":"","first":false,"last":false,"index":872,"pageCount":1101}}

